/* Fonts */

$roboto-reg: 'Roboto-Regular', Arial, Helvetica, sans-serif;
$roboto-lig: 'Roboto-Light', Arial, Helvetica, sans-serif;
$roboto-bold: 'Roboto-Bold', Arial, Helvetica, sans-serif;
$asap-reg: 'Asap-Regular', Arial, Helvetica, sans-serif;
$robotoslab-reg: 'RobotoSlab-Regular', Arial, Helvetica, sans-serif;

@font-face {
    font-family: 'Roboto-Regular';
    src: url(../fonts/Roboto-Regular.ttf);
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto-Light';
    src: url(../fonts/Roboto-Light.ttf);
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto-Bold';
    src: url(../fonts/Roboto-Bold.ttf);
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Asap-Regular';
    src: url(../fonts/Asap-Regular.ttf);
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'RobotoSlab-Regular';
    src: url(../fonts/RobotoSlab-Regular.ttf);
    font-weight: normal;
    font-style: normal;
}

/* Grid */
$yo: purple;
body {
    padding: 0;
    margin: 0;
    background-color: $yo;
}
.row {
    display: flex;
}
.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
    padding: 0 15px;
}
.col-12{width: 100%;}
.col-11{width: 91.66666667%;}
.col-10{width: 83.33333333%;}
.col-9 {width: 75%;}
.col-8 {width: 66.66666667%;}
.col-7 {width: 58.33333333%;}
.col-6 {width: 50%;}
.col-5 {width: 41.66666667%;}
.col-4 {width: 33.33333333%;}
.col-3 {width: 25%;}
.col-2 {width: 16.66666667%;}
.col-1 {width: 8.33333333%;}
.text-left {text-align: left;}
.text-center {text-align: center;}
.text-right {text-align: right;}
ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.hidden {
    visibility: hidden;
    padding: 0;
    margin: 0;
    width: 0;
    height: 0;
    font-size: 0;
    line-height: 0;
}
button {
    background-color: transparent;
    border: 0;
    cursor: pointer;
    outline: none;
}

/* Main styles */

body {
    font-family: $roboto-reg;
}
.content {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1170px;
}

